import {$qs} from '../../../../../@shared/ts/dom';
import {type StripeGateway, type IStripeContext} from '../../../models/stripe';
import {MerchantConfiguration} from '../../../reducers/merchantConfigurationReducer';
import {PeachPayCustomer} from '../../../reducers/peachPayCustomerReducer';
import {getLocaleText} from '../../../util/translation';
import {createSetupIntent} from '../setup-intent';
import bankImageURL from '../../../../img/marks/bank.svg';
import {Feature} from '../../../reducers/environmentReducer';
import {FeatureFlag} from '../../../../../@type/features';

// https://stripe.com/docs/payments/ach-debit/accept-a-payment
function initStripeACHDebitMethod(): StripeGateway {
	return {
		config: {
			name: Feature.metadata<string>(FeatureFlag.STRIPE_ACHDEBIT_GATEWAY, 'title') ?? getLocaleText('US bank account'),
			description: /* html */ `<p style="margin: 0;">${getLocaleText('After selecting <b>Pay</b> a prompt will appear to select a bank account.')}</p><hr><p class="muted" style="margin: 0;">${
				getLocaleText('Note: Payments may take up to 5 days to complete.')
			}</p>`,
			gatewayId: 'peachpay_stripe_achdebit',
			assets: {
				title: {src: bankImageURL},
				badge: {src: bankImageURL},
			},
		},
		async tokenize(context: IStripeContext, sessionId: string) {
			const {stripe} = context;

			const clientSecret = await createSetupIntent(sessionId, 'us_bank_account');
			if (!clientSecret) {
				return [null, {
					paymentStatus: 'creationfailed',
					note: 'Failed to create setup intent.',
				}];
			}

			const {error: collectError, setupIntent: collectIntent} = await stripe.collectBankAccountForSetup({
				clientSecret,
				params: {
					payment_method_type: 'us_bank_account',
					payment_method_data: {
						billing_details: {
							email: PeachPayCustomer.billing.email(),
							name: PeachPayCustomer.billing.firstName() + ' ' + PeachPayCustomer.billing.lastName(),
						},
					},
				},
			});
			if (!collectIntent || collectError) {
				return [null, {
					paymentStatus: 'creationfailed',
					note: collectError?.message ?? 'Failed to collect bank account for setup intent.',
				}];
			}

			if (collectIntent.status !== 'requires_confirmation') {
				return [null, {
					paymentStatus: 'creationfailed',
					note: 'The customer canceled choosing a bank account.',
				}];
			}

			const mandateResponse = await displayMandate({
				fullName: PeachPayCustomer.billing.fullName(),
				clientName: MerchantConfiguration.name(),
			});
			if (!mandateResponse) {
				const message = getLocaleText('The required mandate was not accepted. Payment canceled.');
				return [null, {note: message}];
			}

			const {error: confirmError, setupIntent: confirmIntent} = await stripe.confirmUsBankAccountSetup(clientSecret);
			if (!confirmIntent || confirmError) {
				return [null, {
					paymentStatus: 'creationfailed',
					note: confirmError?.message ?? 'Failed to confirm setup intent.',
				}];
			}

			if (confirmIntent.status !== 'succeeded') {
				return [null, {
					paymentStatus: 'creationfailed',
					note: `Setup intent resulted in invalid status(${confirmIntent.status})`,
				}];
			}

			return [confirmIntent.payment_method as string, {}];
		},
		async confirm(context: IStripeContext, clientSecret: string) {
			const {stripe} = context;

			const {paymentIntent, error} = await stripe.retrievePaymentIntent(clientSecret);

			if (!paymentIntent || error) {
				const message = error?.message ?? 'Failed to retrieve payment intent.';
				return [null, {
					note: message,
				}];
			}

			if (paymentIntent.status === 'succeeded' || paymentIntent.status === 'processing') {
				return ['success', {}];
			}

			return [null, {
				note: `Failed to confirm payment intent with status(${paymentIntent.status})`,
			}];
		},
	};
}

async function displayMandate(options: IMandateOptions): Promise<boolean> {
	return new Promise((res, rej) => {
		$qs('body', $el => {
			$el.insertAdjacentHTML('beforeend', mandateTemplate(options));
		});

		const $container = $qs<HTMLDivElement>('#ach-mandate-container');
		const $acceptButton = $qs<HTMLButtonElement>('#ach-button-accept');
		const $cancelButton = $qs<HTMLButtonElement>('#ach-button-cancel');
		const $xButton = $qs<HTMLButtonElement>('#ach-button-x');

		if (!$container || !$acceptButton || !$cancelButton || !$xButton) {
			$container?.remove();
			rej(new Error('Developer error: Mandate was not mounted.'));
			return;
		}

		$acceptButton.addEventListener('click', () => {
			$container.remove();
			res(true);
		});

		$cancelButton.addEventListener('click', () => {
			$container.remove();
			res(false);
		});

		$xButton.addEventListener('click', () => {
			$container.remove();
			res(false);
		});
	});
}

interface IMandateOptions {
	fullName: string;
	clientName: string;
}

